<template>
  <div :class="style" ref="like">
    <div class="like-vote">
      <p>{{content}}</p>
      <div>‚ô•{{counter}}</div>
    </div>
  </div>
</template>

<script>
import http from "../http";

export default {
  name: "Like",
  props: {
    text: {
      type: String,
      default: ""
    },
    direction: {
      required: true,
      type: String
    }
  },
  watch: {
    result(newVal) {
      this.requestId = requestAnimationFrame(this.up);
      return newVal;
    }
  },
  data() {
    return {
      time: 5, //ÊåÅÁª≠ÁöÑÁßíÊï∞
      updateTime: 10, //getLikeÊõ¥Êñ∞ÊåÅÁª≠ÁöÑÁßíÊï∞
      counter: 0, //Êìç‰ΩúÁöÑËÆ°Êï∞Âô®
      result: 0, //ËÆ°Êï∞Âô®ÁöÑÁªìÊûú
      startValue: 0, //ËÆ°Êï∞Âô®ÁöÑÂàùÂßãÂÄº
      startTime: 0, //Â∏ßÂºÄÂßãÊó∂Èó¥
      frame: 0, //ÂΩìÂâçÂ∏ßÁöÑÂÄº
      requestId: null, //ÂΩìÂâçÂ∏ßÊï∞
      content: this.text, //textÂ±ïÁ§∫ÂâØÊú¨
      lastClickTime: 0 //Âä†ÂøÉÊúÄÂêéÁÇπÂáªÊó∂Èó¥
    };
  },
  mounted() {
    setTimeout(() => {
      this.$refs.like.addEventListener("click", this.addLike, false); //Êï∞Â≠óÊªöÂä®ÂâçÊó†Ê≥ïÁÇπÂáª
    }, this.duration);
    this.getLike();
    this.requestId = requestAnimationFrame(this.up);
  },
  methods: {
    //Â∏ßÊìç‰Ωú
    up(timestamp) {
      if (!this.startTime) {
        this.startTime = timestamp;
      }
      let progress = timestamp - this.startTime;

      if (this.startValue > this.result) {
        //‰ªéÂ∞èÂà∞Â§ß
        this.frame =
          this.startValue -
          this.easing(progress, 0, this.startValue - this.result);

        //‰øùËØÅÂΩìÂâçÂ∏ßÁöÑÂÄº‰∏çË∂ÖËøáÁªìÊûú
        this.frame = this.frame < this.result ? this.result : this.frame;
      } else {
        this.frame = this.easing(progress, 0, this.result - this.startValue);
        this.frame = this.frame > this.result ? this.result : this.frame;
      }

      this.frame = Math.round(this.frame);
      this.counter = this.startValue + this.frame;

      if (progress < this.duration) {
        this.requestId = requestAnimationFrame(this.up);
      }
    },
    //Êõ¥Êñ∞Â∏ß
    update(newVal) {
      newVal = Number(newVal);
      if (newVal === this.frame) return;
      cancelAnimationFrame(this.requestId);
      this.startTime = 0;
      this.startValue = this.counter;
      this.result = newVal;
      this.requestId = requestAnimationFrame(this.up);
    },
    //ÁºìÂÜ≤Âä®Áîª
    easing(t, b, c) {
      return (
        (c * (-Math.pow(2, (-10 * t) / this.duration) + 1) * 1024) / 1023 + b
      );
    },
    async getLike() {
      let result = await http.getLike("5e54eeacff028300082a8d02");

      try {
        result = JSON.parse(result);
        if (result.hasOwnProperty("count")) {
          this.update(result.count);
        }
      } catch (err) {
        console.error(err);
      }

      //ÂÆöÊó∂Ëß¶ÂèëÊõ¥Êñ∞
      setTimeout(this.getLike, this.updateDuration);
    },
    addLike() {
      //ÊéßÂà∂ÁÇπÂáªÈó¥ÈöîÁöÑÊñáÂ≠ó
      let currClickTime = new Date().getTime();
      let flag = currClickTime - this.lastClickTime > 300;
      this.lastClickTime = new Date().getTime();
      if (flag) {
        this.content = "Áà±‰Ω†Âìü(¬¥‚ñΩ` É‚ô°∆™)";
      } else {
        this.content = "‚àë( Âè£ ||Ë¶ÅÂùèÊéâ‰∫Ü";
      }

      http.addLike("5e54eeacff028300082a8d02");
      this.update(this.counter += 1);
    }
  },
  computed: {
    duration() {
      return Number(this.time) * 1000;
    },
    updateDuration() {
      return Number(this.updateTime) * 1000;
    },
    style() {
      return "like";
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/color.scss";

.like {
  width: 100%;
  height: 100%;
  font-weight: bold;

  &:hover {
    background-color: $LIKE‰∏ªÈ¢òËâ≤;
    transition: all 0.6s;

    p,
    div {
      color: $Â≠ó‰ΩìËâ≤;
    }
  }

  .like-vote {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    cursor: pointer;
    color: $LIKE‰∏ªÈ¢òËâ≤;

    p {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    div {
      position: absolute;
      left: 50%;
    }
  }
}
</style>